import React, { useState } from 'react';

import { Div } from "./style";
import Input from './components/SearchBox/Input'

import Untitleddesign from './img/Untitled design.png'


function App() {

  { /*const [contagem, setContagem] = useState(0)*/ }

  { /* function soma() {
    setContagem(contagem + 1)
  }
  function menos() {
    setContagem(contagem - 1)
  }*/}

  const [texto, setTexto] = useState(0)

  function onchange(e: any) {
    setTexto(e.target.value)
  }

  const [precoTotal, setPrecoTotal] = useState(0)

  function onPreco(e: any) {
    setPrecoTotal(e.target.value)
  }



  return (
    <Div>
      <header>
        <img src={Untitleddesign} className="logo" alt="logo" />
        <nav>
          <div className="divs">
            <a href="https://github.com/Dev-Verissimo">
              <img src="https://upload.wikimedia.org/wikipedia/commons/9/91/Octicons-mark-github.svg" alt="" />
            </a>
          </div>
          <div className="divs">
            <a href="https://www.linkedin.com/in/front-endverissimo/">
              <img src="https://upload.wikimedia.org/wikipedia/commons/e/e9/Linkedin_icon.svg" alt="" />
            </a>
          </div>
        </nav>

      </header>
      <h1>Calculadora de Gorjeta</h1>
      <div id="divInput">
        <div className="divInput">
          <label>
            Informe o valor da conta:
          </label>
          <Input texto={precoTotal} function={onPreco} />

        </div>
        <div className="divInput">
          <label>Qual é a porcentagem da gorjeta?</label>
          <Input texto={texto} function={onchange} />
        </div>
      </div>


      <div id="resultado">
        <p>
          Sub-total: {precoTotal}
        </p>
        <p>
          Gorjeta ({texto}%)
        </p>
      </div>
      <p>
        O valor total é:
        {Number(precoTotal) + Number(precoTotal * texto / 100)}
      </p>


      {/* <button className="one" onClick={soma}>
        Clique para somar
      </button>
      <button onClick={menos}>
        Clique para decrescer
      </button> */}
    </Div>
  );
}

export default App;
